#index-container {
  width: 100%;
}

#index-main {
  display: flex;
  align-items: center;
  height: 25%;
  background-color: blue;
  width: 25%;
  margin: auto;
}

#header-img {
  position: absolute;
  height: 100px;
}

.header-text {
  text-align: center;
  position: absolute;
  color: black;
  letter-spacing: 3px;
  font-size: 27px;
  font-weight: 800;
  z-index: 1;
  margin: auto;
  text-shadow: -1px -1px 0 #FFF, 1px -1px 0 #FFF, -1px 1px 0 #FFF, 1px 1px 0 #FFF;
}

.index-header {
  height: 80px;
  background-color: #555794;
}

.index-header-center {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.index-header-col {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-around;
  text-align: center;
}

.index-body {
  height: 500px;
}

.index-body-center {
  width: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.index-input {
  width: 100%;
  text-align: center;
  box-shadow: 0 0 20px gray;
}

.menu-nav-item {
  position: relative;
  background-color: #555794;
  height: 100%;
  flex: 1;
  text-align: center;
}

.menu-nav-item-link {
  color: black;
  letter-spacing: 2px;
}

.menu-nav-item-link:hover {
  text-decoration: none;
  text-shadow: 1px 0px 0px gray;
}

.menu-nav-item span {
  line-height: 80px;
}

.menu-nav-item:hover {
  background-color: #7F81B2;
}

.menu-nav-dropdown {
  position: absolute;
  top: 100%;
  left: 0px;
  width: 100%;
  overflow: hidden;
}

.menu-nav-dropdown-item {
  border-radius: 20px;
  background-color: gray;
  height: 30px;
}

.menu-nav-dropdown-item:hover {
  background-color: black;
  color: white;
}